//-------------------- mixins -----------------------

//media queries
@mixin large {
  @media only screen and (max-width: $large-width + 30px) {
    @content;
  }
}

@mixin xlarge {
  @media only screen and (min-width: $xlarge-width) {
    @content;
  }
}


  @mixin desktop {
    @media only screen and (max-width: $desktop-width + 30px) {
      @content;
    }
  }
  @mixin tablet {
    @media only screen and (max-width: $tablet-width) {
      @content;
    }
  }
  @mixin mobile {
    @media only screen and (max-width: $mobile-width) {
      @content;
    }
  }




  @mixin desktop1200 {
    @media only screen and (max-width: $desktop-width1200) {
      @content;
    }
  }

@mixin largeDesktop {
  @media only screen and (max-width: $desktop-width1450) {
    @content;
  }
}

@mixin tabletToPhone {
  @media screen and (min-device-width: $mobile-width) and (max-device-width: $tablet-width) {
    @content;
  }
}

