/*-------------------- global-styles --------------------*/
html {
  min-width: 320px;
  background: #fff;
}

body {
  min-width: $large-width;
  height: 100%;
  color: #000;
  font-family: 'Gotham', sans-serif;
  font-size: 16px;
  overflow-x: hidden !important;


  @include large {
    min-width: 320px;
  }
}

::selection {
  color: #fff;
  background: $base-color;
}

input:-webkit-autofill {
  box-shadow: 0 0 0 30px #fff inset;
}

a {
  color: #000;
  text-decoration: none;
  transition: all .3s;
}

.global-wrap {
  display: flex;
  width: 100vw;
  flex-direction: column;

  .main {
    flex: 1 0 auto;
    /* NOT flex: 1; */
  }
}

.wrap {
  position: relative;
  overflow: hidden;
  width: $large-width;
  margin: 0 auto;
  padding: 0 10px;

  @include large {
    width: 100%;
  }
}

.btn {
  position: relative;
  display: inline-block;
  vertical-align: top;
  min-width: 170px;
  text-align: center;
  color: #fff;
  font-size: 18px;
  line-height: 1.15;
  font-weight: 900;
  letter-spacing: 0.36px;
  padding: 9px 20px;
  background: $base-color;
  border: 1px solid $base-color;
  transition: all .3s;

  &:hover {
    color: $base-color;
    background: transparent;
  }

  &.reverse {
    color: $base-color;
    background: transparent;

    &:hover {
      color: #fff;
      background: $base-color;
    }
  }

  &.loading,
  &.added,
  &.error {
    padding-right: 45px;

    &:after {
      content: '';
      position: absolute;
      top: 0;
      bottom: 0;
      right: 20px;
      margin: auto;
      width: 16px;
      height: 16px;
      background-size: contain;
    }
  }

  &.loading {
    &:after {
      background: url('../img/svg-icons/loading.svg') no-repeat center;
      background-size: contain;
    }
  }

  &.added {
    &:after {
      background: url('../img/svg-icons/checked.svg') no-repeat center;
      background-size: contain;
    }
  }

  &.error {
    &:after {
      background: url('../img/svg-icons/error.svg') no-repeat center;
      background-size: contain;
    }
  }
}

/*fields styles*/
.fields-row {
  +.fields-row {
    margin-top: 20px;

    @include tablet {
      margin-top: 15px;
    }

    @include mobile {
      margin-top: 10px;
    }
  }

  .field-wrap {
    position: relative;

    input:not([type="radio"]):not([type="checkbox"]) {
      display: block;
      width: 100%;
      height: 40px;
      padding: 0 10px;
      color: #373531;
      font-weight: 900;
      font-size: 16px;
      border: 1px solid #373531;
    }

    textarea {
      display: block;
      width: 100%;
      height: 40px;
      padding: 5px 10px;
      color: #373531;
      font-weight: 900;
      font-size: 16px;
      border: 1px solid #373531;
    }
  }

  &.two-in-row {
    display: flex;
    justify-content: space-between;

    @include mobile {
      display: block;
    }

    .field-wrap {
      width: 48%;

      @include mobile {
        width: 100%;

        +.field-wrap {
          margin-top: 10px;
        }
      }
    }
  }

  &.three-in-row {
    display: flex;
    justify-content: space-between;

    @include tablet {
      display: block;
    }

    .field-wrap {
      width: 32%;

      @include tablet {
        width: 100%;

        +.field-wrap {
          margin-top: 10px;
        }
      }
    }
  }
}



