      }
    }
  }

  &__buttons {
    padding-top: 13px;
    padding-bottom: 12px;
    padding-left: 67px;

    @media (max-width: 838px) {
      padding-left: 0;
    }

    .nice-select:after {
      border-bottom: 2px solid #96a6a7;
      border-right: 2px solid #96a6a7;
    }

    .nav__dropdown-select {
      margin-right: 12px;
      box-shadow: 0 1px 0 #edebec;
      background-color: #ffffff;
      min-width: 115px;
      height: 35px;
      line-height: 33px;
      color: #e94e38;
      font-size: 12px;
      font-weight: 500;
      text-transform: uppercase;
      border-radius: 15px;

    }

    .map {
      width: 125px;
      height: 35px;
      border: 1px solid #96a6a7;
      background-color: #fff;
      color: rgba(86, 99, 117, 0.3);
      color: #96a6a7;

      font-size: 12px;
      font-weight: 500;
      border-radius: 15px;
      text-transform: uppercase;
    }


  }

  &__filter {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;

    .filter-text {
      color: #96a6a7;

      font-size: 12px;
      font-weight: 500;
      line-height: 48px;
      text-transform: uppercase;
    }

    .checkboxes {
      display: flex;
      flex-direction: row;
      flex-wrap: nowrap;
      justify-content: center;
      align-items: center;

      .check-wrap {
        display: flex;
        flex-direction: row;
        justify-content: center;
        align-items: center;
        flex-wrap: nowrap;
      }

      [type="checkbox"]:not(:checked),
      [type="checkbox"]:checked {
        position: absolute;
        left: -9999px;
      }

      [type="checkbox"]:not(:checked)+label,
      [type="checkbox"]:checked+label {
        position: relative;
        padding-left: 1.95em;
        margin-left: 25px;
        cursor: pointer;

        @media (max-width: 350px) {
          margin-left: 18px;
        }
      }

      /* checkbox aspect */
      [type="checkbox"]:not(:checked)+label:before,
      [type="checkbox"]:checked+label:before {
        content: '';
        position: absolute;
        top: 50%;
        left: 50%;
        margin-right: -50%;
        transform: translate(-50%, -50%);
        width: 40px;
        height: 40px;


        background: #1baf5d url("../img/svg-icons/filter1.svg") center no-repeat;

        border-radius: 50px;
        box-shadow: inset 0 1px 3px rgba(0, 0, 0, .1);
      }

      [type="checkbox"]:not(:checked)+label:before {
        background-color: #b6c0c1;
      }

      [type="checkbox"]:not(:checked)+label.girl:before {

        background: #ec407a url("../img/svg-icons/filter2.svg") center no-repeat;
      }

      [type="checkbox"]:checked+label.girl:before {

        background: #b6c0c1 url("../img/svg-icons/filter2.svg") center no-repeat;

      }

      [type="checkbox"]:not(:checked)+label.study:before {
        background: #03a9f4 url("../img/svg-icons/filter3.svg") center no-repeat;
      }

      [type="checkbox"]:checked+label.study:before {
        background: #b6c0c1 url("../img/svg-icons/filter3.svg") center no-repeat;
      }






      [type="checkbox"]:checked+label.music:before {
        background: #f19e1f url("../img/svg-icons/filter4.svg") center no-repeat;
        background-position-x: 9px;
      }

      [type="checkbox"]:not(:checked)+label.music:before {
        background: #b6c0c1 url("../img/svg-icons/filter4.svg") center no-repeat;
        background-position-x: 9px;
      }





      [type="checkbox"]:checked+label.bus:before {
        background: #88499c url("../img/svg-icons/filter5.svg") center no-repeat;
      }

      [type="checkbox"]:not(:checked)+label.bus:before {
        background: #b6c0c1 url("../img/svg-icons/filter5.svg") center no-repeat;

      }

      /* checked mark aspect */


      /* checked mark aspect changes */
      [type="checkbox"]:not(:checked)+label:after {
        opacity: 0;
        transform: scale(0);
      }

      [type="checkbox"]:checked+label:after {
        opacity: 1;
        transform: scale(1);
      }

      /* disabled checkbox */
      [type="checkbox"]:disabled:not(:checked)+label:before,
      [type="checkbox"]:disabled:checked+label:before {
        box-shadow: none;
        border-color: #bbb;
        background-color: #ddd;
      }

      [type="checkbox"]:disabled:checked+label:after {
        color: #999;
      }

      [type="checkbox"]:disabled+label {
        color: #aaa;
      }
    }

  }
}

