  padding-top: 49px;
  background-color: #f3f1f2;

  .container {
    max-width: 947px;
    width: 100%;
    margin: 0 auto;

    .list-items {
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      align-content: center;

      @media (max-width: 1024px) {
        flex-wrap: wrap;
        justify-content: center;

      }

      .item {
        width: 100%;
        max-width: 455px;

        &:first-child {
          margin-right: 20px;
        }

        @media (max-width: 1024px) {
          margin-top: 15px;



        }

        @media (max-width: 929px) {
          &:first-child {
            margin-right: 0;
          }
        }




        .up-block {
          position: relative;

        }

        .feedback {
          border-top: 3px solid #1baf5d;
          background-color: #fff;
          padding-left: 87px;
